.global--main-nav {
    width: 100%;
    height: 90px;
    padding-left: 0.75em;
    padding-right: 0.75em;
    position: fixed;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #fff;
    z-index: 100;
    transition: 0.35s ease-in;
    @include mq (medium) {
        padding-left: 1.75em;
        padding-right: 1.75em;
    }

    &-left {
        img {
            margin-top: 5px;
            width: 140px;
        }
    }

    &-right {
        display: none;
        @include mq (medium) {
            display: block;
        }
    }

    &-item {
        padding-left: 0.75em;
        padding-right: 0.75em;
        color: #333E48;
        text-transform: uppercase;
        font-size: 0.95rem;

        &:hover {
            color: #A0A4B8;
            text-decoration: none;
        }
    }

    &-mobile {
        display: block;
        @include mq (medium) {
            display: none;
        }

        button {
            font-size: 1.15rem;
            border: none;
            cursor: pointer;
        }
    }
}

.headroom--not-top {
    height: 75px;
    transition: 0.35s ease-in;

    img {
        margin-top: 7px;
        width: 100px;
    }

    .global--main-nav-item {
        font-size: 0.75rem;
    }
}

.mobile-menu-open {
    width: 100%;
    height: 250px;
    position: absolute;
    left: 0;
    top: 90px;
    background: white;
    display: flex;
    flex-direction: column;
}
